/* App.css: extracted styles from App.tsx */

.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background: #222;
  color: #fff;
}

.app-title {
  margin: 0;
  font-size: 1.2rem;
}

.app-main {
  max-width: 600px;
  margin: 2rem auto;
  background: #fff;
  padding: 24px;
  border-radius: 8px;
  box-shadow: 0 2px 8px #0001;
}

.form-row {
  display: flex;
  gap: 8px;
  margin-bottom: 24px;
}

.form-label {
  font-weight: 600;
  display: block;
  align-self: center;
}

.input-url {
  flex: 1;
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.submit-btn {
  padding: 0 18px;
  border-radius: 4px;
  border: none;
  background: #222;
  color: #fff;
  font-weight: 500;
  cursor: pointer;
}

.prompt-prefix-section {
  margin-bottom: 16px;
}

.prompt-prefix-label {
  font-weight: 600;
  display: block;
  margin-bottom: 4px;
}

.prompt-prefix-input {
  width: 100%;
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #ccc;
  margin-bottom: 4px;
}

.prompt-prefix-desc {
  color: #666;
  font-size: 13px;
  margin-bottom: 0;
}

.models-section,
.prompts-section {
  margin-bottom: 16px;
}

.models-list,
.prompts-list {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  margin-top: 6px;
}

.model-label,
.prompt-label {
  display: flex;
  align-items: center;
  gap: 4px;
}

.post-content-section {
  margin-bottom: 24px;
  padding: 12px;
  background: #f6f8fa;
  border-radius: 6px;
  border: 1px solid #eee;
}

.post-content-text {
  margin-top: 8px;
  white-space: pre-wrap;
  font-family: inherit;
  font-size: 15px;
}

.error-message {
  color: #c00;
  margin-bottom: 12px;
}

.matrix-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 24px;
}

.matrix-th,
.matrix-td,
.matrix-prompt {
  border: 1px solid #ccc;
  padding: 8px;
}

.matrix-th {
  font-weight: 600;
}

.matrix-td {
  font-family: inherit;
  font-size: 14px;
  background: #fafbfc;
  vertical-align: top;
}

.matrix-prompt {
  font-weight: 500;
}

.loading {
  color: #888;
}

.queued {
  color: #aaa;
}

.cell-error {
  color: #c00;
}

.kv-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.kv-list-item {
  margin-bottom: 4px;
}

.kv-key {
  font-weight: 600;
}
